@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
:root {
  --dark-blue: hsl(209, 23%, 22%);
  --very-dark-blue: hsl(207, 26%, 17%);
  --white: hsl(0, 0%, 100%);
}

body {
  font-family: "Nunito Sans", sans-serif;
  background: hsl(207, 26%, 17%);
  background: var(--very-dark-blue);
  color: hsl(0, 0%, 100%);
  color: var(--white);
}
.loading {
  color: hsl(0, 0%, 100%);
  color: var(--white);
}
.form-control,
.form-control:focus {
  background: var(--dark-blue);
  color: var(--white);
  padding: 1rem;
  padding-left: 0;
  border: transparent;
  box-shadow: none;
}
.form-control::-webkit-input-placeholder{
  color: var(--white);
}
.form-control:-ms-input-placeholder{
  color: var(--white);
}
.form-control::placeholder{
  color: var(--white);
}
.input-group-text{
  border: transparent;
  background: var(--dark-blue);
  color: var(--white);
  padding: .6rem 1rem;
}

.card{
  background: var(--dark-blue);
  color: var(--white);
  text-decoration: none;
}
.card:hover{
  color: var(--white);
}
.card:visited{
  color: var(--white);
}
.card img {
  height: 12.5rem;
  object-fit: cover;
}
.card .card-title {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow-y: hidden;
  letter-spacing: 1px;
}
.card .card-text{
  margin-bottom: 0.5rem;
  letter-spacing: 1px;
}
.card .card-text strong{
  font-weight: 600;
}

@media screen and (max-width: 576px) {
  .card img {
    height: 16rem;
  }
}

header{
  background: var(--dark-blue);
}

header section{
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

header h4{
  font-weight: 600;
}
/* Country Details */
.btn-back{
  color: var(--white);
  background: var(--dark-blue);
  text-decoration: none;
  padding: 0.6rem 2rem;
  border-radius: 5px;
  display: inline-block;
}
.btn-back:hover{
  color: var(--white);
}
.flag{
  width: 100%;
}
.country-details strong{
  text-transform: capitalize;
  font-weight: 600;
}
.borders-list{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}
.borders-list li{
  background: var(--dark-blue);
  padding: 0.5rem 1rem;
  margin-right: 0.8rem;
  border-radius: 5px;
}
